.cardSeries{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 250px;
    background-color: #222222;
    text-align: center;
    border-radius: 10px;
}

.cardImg{
    width: 250px;
}
.cardImg img{
    width: 100%;
    border-radius: 10px 10px 0 0;
}

.cardInfo{
    color: white;
}

.cardSeries a{
    text-decoration: none;
}

.cardSeries button{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: cornflowerblue;
    color: #FFF;
    font-weight: bold;
    font-size: 15px;
    border-radius: 5px;
    border: none;
    width: 150px;
    padding: 5px;
    cursor: pointer;
    transition: 1s;
    margin-bottom: 5px;
}

.cardSeries button:hover{
    opacity: 0.7;
}

.average{
    display: flex;
    align-items: center;
    justify-content: center;
}

