.navlink{
    text-decoration: none;
    margin: 10px;
    color: white;
    font-weight: bold;
    cursor: pointer;
    transition: 1s;
}

.navlink.active{
    color: cornflowerblue;
    border-bottom: 1px solid;
}

.navlink:hover{
    opacity: 0.7;
}

.menu{
    background-color: black;
    margin-left: 70px;
    margin-top: 10px;
}