.btnBack{
    display: flex;
    align-items: center;
    color: #FFF;
    background-color: transparent;
    font-size: 20px;
    border: 2px solid #c4c4c4;
    border-radius: 10px;
    cursor: pointer;
    align-self: flex-start;
    margin: 20px;
    transition: 1s;
}
.btnBack:hover{
    opacity: 0.5;
}

.hero{
    display: flex;
    position: relative;
    z-index: 5;
}

.hero .bg{
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    height: 92vh;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -1;
    opacity: .5;
    box-shadow: inset 0px -100px 100px #000;
}

.hero img{
    border-radius: 15px;
    max-height: 400px;
    margin: 20px;
}

.serieInfo h1{
    color: #FFF;
}

.serieInfo{
    color: #c4c4c4;
    margin-left: 20px;
}

.serieInfo p{
    font-size: 22px;
    text-align: justify;
}

.score{
    display: flex;
    align-items: center;
}
.score span{
    margin-left: 10px;
}

.overview{
    width: 50%;
}
